*{
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
}
$width:1200px;
$fontSize:14px;
#main {
    width: 1000px;
    margin: 0 auto;
    /* background: lightcyan; */
}
.footer-p{
    font-size: 18px;
    color: #333;
    padding-left: 52px;
    height: 102px;
    line-height: 102px;
    position: absolute;
    top: 0;
}
.footer-i{
    position: absolute;
    left: 0;
    top: 30px;
    width: 42px;
    height: 42px;
    background: url(../img/nav-icon.png) 0 0 no-repeat;
}
.prize-i{
    width: 90px;
    height: 90px;
    display: block;
    margin: 0 auto;
    background: url(../img/nav-icon.png) 0 0 no-repeat;
    margin-bottom: 20px;
}
.prize-item{
    position: absolute;
    top: 30px;
    text-align: center;
}
/*logo部分*/
.logo{
    margin: 24px 0 99px 0;
    height: 51px;
    /* background: lightcoral; */
    a{
        display: inline-block;
        float: left;
        text-decoration: none;
    }
    img{
        display: block;
    }
    span{
        display: inline-block;
        height: 51px;
        color: #231815;
        font-size: 20px;
        line-height: 70px;
        padding-left: 20px;
    }
}
/*表单上的文字部分*/
#menu{
    width: 1000px;
    /* background: lightgreen; */
    h3{
        font-size: 24px;
        font-weight: bold;
        color: #222222;
        text-align: center;
        line-height: 1;
        margin-bottom: 68px;
    }
    .loginBox{
        height: 25px;
        width: 300px;
        /* background: lightseagreen; */
        margin: 0 0 0 348px;
        position: relative;
        span{
            font-size: 17px;
            color: #666666;
        }
        p{
            position: absolute;
            right: 0;
            top: 0;
            font-size: 14px;
            color: #999999;
        }
    }
    a{
        color: #fd8900;
        text-decoration: none;
    }
}
/*表单部分*/
#registerBox {
    margin-left: 250px;
    .label{
        width: 7em;
        font-size: 14px;
        text-align: right;
        line-height: 46px;
        color: #333333;
    }
    .input{
        outline:none;
        width: 300px;
        height: 46px;
    }
    .row{
        padding: 10px 0;
    }
    .lastrow{
        padding: 10px 0 25px 0;
    }
    .agree{
        font-size: 14px;
        color: #999999;
        padding-left: 99px;
    }
    .send{
        padding-left: 99px;
        input{
            width: 300px;
            height: 50px;
            border: 0 none;
            cursor: pointer;
            font-size: 20px;
            color: #fff;
            background: orange;
        }
    }
}

/* 底部 */
#footer{
    background-color: #fff;
    overflow: hidden;
    border-top: 1px solid #ebebeb;
    .footerMain{
        width: 1200px;
        margin: 0 auto;
        position: relative;
        overflow: hidden;
        /* 保证 */
        .footer-promise{
            height: 102px;
            background-color: #fff;
            position: relative;
            border-bottom: 1px solid #dedede;
            .footer-p1{
                @extend .footer-p;
                left: 20px;
                i{
                    @extend .footer-i;background-position: 0 -360px;
                }
            }
            .footer-p2{
                @extend .footer-p;
                left: 380px;
                i{
                    @extend .footer-i;background-position: -43px -360px;
                }
            }
            .footer-p3{
