                    @extend .footer-i;background-position: -86px -360px;
                }
            }
            .footer-p4{
                @extend .footer-p;
                left: 1025px;
                i{
                    @extend .footer-i;background-position: -129px -360px;
                }
            }
        }
        /* 列表 */
        .footer-list{
            padding: 30px 0;
            border-bottom: 1px solid #dedede;
            overflow: hidden;
            .list{
                width: 200px;
                float: left;
            }
            h4{
                color: #333;
                font-size: 16px;
                line-height: 1;
                margin-bottom: 14px;
                font-weight: normal;
            }
            .innerList li{
                margin-bottom: 10px;
            }
            .innerList li a{
                color: #666;
                font-size: 14px;
                &:hover{
                    color: #ff7f00;
                }
            }
        }
        /* 奖项 */
        .footer-prize{
            height: 203px;
            position: relative;
            // float: left;
