                i{
                    @extend .prize-i;
                    background-position: -182px -178px;
                }
            }
            .prize-item4{
                @extend .footer-prize;
                width: 144px;
                left: 389px;
                i{
                    @extend .prize-i;
                    background-position: -273px -178px;
                }
            }
            .prize-item5{
                @extend .footer-prize;
                width: 90px;
                left: 552px;
                i{
                    @extend .prize-i;
                    background-position: -364px -178px;
                }
            }
            .prize-item6{
                @extend .footer-prize;
                width: 125px;
