                i{
                    @extend .prize-i;
                    background-position: -273px -269px;
                }
            }
        }
    }
    /* 版权 */
    .footer-copyright{
        padding: 30px 0;
        background-color: #f7f7f7;
        .footer-copyright-main{
            width: 1200px;
            margin: 0 auto;
            position: relative;
            overflow: hidden;
            .copyright-p1{
                font-size: 18px;
                color: #999;
                line-height: 24px;
                margin-bottom: 9px;
            }
            .copyright-p2{
                font-size: 12px;
                color: #999;
                line-height: 1;
            }
            .copyright-a{
                width: 108px;
                height: 45px;
                display: block;
                background: url(../img/nav-icon.png) -392px 0 no-repeat;
